<template>
    <div :class="{'col-md-3': true, 'col-sm-3': true, show: leftbarmenu, showmenu: isMenu, hiddenmenu: !isMenu}" id="user-leftbar">
        <div class="box-content">
            <div class="box box-solid" id="box-user">
                <div class="box-header with-border">
                    <h3 class="box-title">Tài khoản</h3>
                    <div v-if="isMenu" class="box-tools">
                        <button v-on:click="togger" type="button" class="btn btn-box-tool" data-widget="collapse"><i class="fa fa-minus"></i>
                        </button>
                    </div>
                </div>
                <div class="box-body no-padding">
                    <ul class="nav nav-pills nav-stacked">
                        <li v-on:click="togger" :class="{active: $route.fullPath.indexOf('/tai-khoan/thong-tin-tai-khoan')==0}">
                            <router-link :to="{name: 'user.profile'}">
                                <i class="fa fa-user"></i> Thông tin tài khoản
                            </router-link>
                        </li>
                        <li v-on:click="togger" :class="{active: $route.fullPath.indexOf('/tai-khoan/thong-bao')==0}">
                            <router-link :to="{name: 'user.notification'}">
                                <i class="fa fa-bell"></i> Thông báo
                            </router-link>
                        </li>
                        <li v-on:click="togger" :class="{active: $route.fullPath.indexOf('/tai-khoan/tin-nhan')==0}">
                            <router-link :to="{name: 'user.message'}">
                                <i class="fa fa-envelope"></i> Tin nhắn
                            </router-link>
                        </li>
                        <li v-on:click="togger" :class="{active: $route.fullPath.indexOf('/tai-khoan/gian-hang-cua-ban')==0}">
                            <router-link :to="{name: 'user.aboutstore'}"><i class="fa fa-archive"></i> Quản lý gian hàng</router-link>
                        </li>
                        <li v-on:click="togger" :class="{active: $route.fullPath.indexOf('/tai-khoan/cac-tin-da-luu')==0}">
                            <router-link :to="{name: 'user.productfollow'}"><i class="fa fa-rss"></i> Các tin đã lưu</router-link>
                        </li>
                        <!-- <li v-on:click="togger"><a href="#"><i class="fa fa-filter"></i> Nap tiền <span class="label label-warning pull-right">500.000 đ</span></a>
                        </li> -->
                    </ul>
                </div>
            </div>
            <div class="box box-solid">
                <div class="box-header with-border">
                    <h3 class="box-title">Tin đăng</h3>
                </div>
                <div class="box-body no-padding">
                    <ul class="nav nav-pills nav-stacked">
                        <li v-on:click="togger" :class="{active: $route.fullPath.indexOf('/tai-khoan/quan-ly-san-pham')==0}">
                            <router-link :to="{name: 'user.product'}">
                                <i class="fa fa-circle-o text-red"></i> Quản lý sản phẩm
                            </router-link>
                        </li>
                        <li v-on:click="togger" :class="{active: $route.fullPath.indexOf('/tai-khoan/day-tin')==0}">
                            <router-link :to="{name: 'user.pushproduct'}">
                                <i class="fa fa-circle-o text-yellow"></i> Đẩy tin
                            </router-link>
                        </li>
                        <li v-on:click="togger" :class="{active: $route.fullPath.indexOf('/tai-khoan/tin-uu-tien')==0}">
                            <router-link :to="{name: 'user.productpriority'}">
                                <i class="fa fa-circle-o text-light-blue"></i> Tin ưu tiên
                            </router-link>
                        </li>
                    </ul>
                </div>
                <!-- /.box-body -->
            </div>
        </div>
        <div v-if="!isMenu" class="box-menu">
            <a v-on:click="togger" class="user-menu-icon btn btn-dropbox "><i class="fa fa-fw fa-align-justify"></i></a>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            leftbarmenu: false,
            leftbarmenuY: null,
            isMenu: false
        }
    },
    methods: {
        togger(){
            this.isMenu = !this.isMenu;
            if(this.isMenu) document.querySelector("body").classList.add('mobile-modal-open');
            else document.querySelector("body").classList.remove('mobile-modal-open');
        },
        showLeftbar(e){
            if(e.path[1].scrollY >= this.leftbarmenuY){
                if(!this.leftbarmenu) this.leftbarmenu = true;
            }
            else if(this.leftbarmenu) this.leftbarmenu = false;
            
        },
        scrollfunction(){
            window.addEventListener("scroll",  this.showLeftbar);
        },
        removeScroll(fn){
           window.removeEventListener("scroll", fn);
        }
    },
    mounted() {
        this.leftbarmenuY = document.querySelector("#user-leftbar").offsetTop - 50;
        this.scrollfunction(); 
    },
    destroyed(){
        this.removeScroll(this.showLeftbar);
        document.querySelector("body").classList.remove('mobile-modal-open');
    }
}
</script>

<style>
    #user-leftbar{
        padding-right: 0px;
        height: 100%;
    }
    #user-leftbar .box-content{
        height: 100%;
        overflow-y: auto;
    }
    #user-leftbar .box-title{
        font-weight: 600;
    }
    #user-leftbar .box-menu .user-menu-icon{
        display: none;
    }
    #box-user {
        margin-bottom: 0px;
    }
    #user-leftbar{
        width: 292.5px;
    }
    .show{
        position: fixed;
        top: 55px;
    }
    @media(max-width: 1199px) {
        #user-leftbar{
            width: 242.5px;
        }
    }
    @media(max-width: 991px) {
        #user-leftbar{
            width: 187.5px;
        }
    }
    @media(max-width: 767px) {
        .mobile-modal-open{
            overflow: hidden;
        }
        #user-leftbar{
            width: 226.5px;
            position: fixed;
            z-index: 1;
            top: 55px;
            display: flex !important;
        }
        #user-leftbar .box-content{
            flex-grow: 1;
        }
        #user-leftbar .box-menu .user-menu-icon{
            display: block;
            position: relative;
            top: 20%;
            padding: 5px 8px;
            border-radius: 0px;
            background: #40a87c;
            opacity: 0.8;
        }
        .showmenu{
            animation: user-show-menu 0.4s forwards;
        }
        .hiddenmenu{
            animation: user-hidden-menu 0.4s forwards;
        }
        @keyframes user-show-menu {
            from{
                transform: translateX(-200px);
            }
            to{
                transform: translateX(0px);
            }
        }
        @keyframes user-hidden-menu {
            from{
                transform: translateX(0px);
            }
            to{
                transform: translateX(-200.5px);
            }
        }
    }
    @media(max-width: 425px){
        #user-leftbar{
            top: 0px;
            left: 0px;
            height: 100%;
            z-index: 1030;
            background: white;
            padding: 0px;
            width: 100%;
        }
        #user-leftbar .box-menu .user-menu-icon{
            position: absolute;
        }
        @keyframes user-show-menu {
            from{
                transform: translateX(-100%);
            }
            to{
                transform: translateX(0%);
            }
        }
        @keyframes user-hidden-menu {
            from{
                transform: translateX(0%);
            }
            to{
                transform: translateX(-99.99%);
            }
        }
    }
</style>